.wrapper {
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

.main {
  flex: auto;
}

body {
  height: 100%;
  letter-spacing: 0;
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.14;
}

a {
