}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
