.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

@keyframes Heart {
  0% {
    transform: scale(1);
  }

  5% {
    transform: scale(1.2);
  }

  10% {
    transform: scale(1);
  }

  15% {
    transform: scale(1.5);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

.rotate-vert-center {
  -webkit-animation: rotate-vert-center 2s cubic-bezier(.455, .03, .515, .955) 3s infinite both;
  animation: rotate-vert-center 4s cubic-bezier(.455, .03, .515, .955) 3s infinite both;
}

@keyframes rotate-vert-center {
  0% {
    -webkit-transform: rotateY(0);
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(360deg);
  }
}

.tracking-in-expand {
  animation: tracking-in-expand 3s cubic-bezier(.215, .61, .355, 1) both;
}

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -.5em;
    opacity: 0;
  }

  40% {
    opacity: .6;
  }

  100% {
    opacity: 1;
  }
}

.loader {
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  animation: loading .5s linear infinite;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  width: 100px;
  height: 100px;
  border: 15px solid #e5e5e5;
  border-color: #e5e5e5 #ff6b08;
  border-radius: 50%;
  animation: loading 75s infinite;
}

@keyframes loading {
  100% {
    transform: rotate(360deg);
  }
}

